@font-face {
    font-family: 'Geometria';
    src: local('Geometria'),
        url('./fonts/Geometria/Geometria-Bold.woff2') format('truetype');
    font-weight: 600;
}

@font-face {
    font-family: 'Geometria';
    src: local('Geometria'),
        url('./fonts/Geometria/Geometria-Medium.woff2') format('truetype');
    font-weight: 500;
}

@font-face {
    font-family: 'Geometria';
    src: local('Geometria'),
        url('./fonts/Geometria/Geometria-Regular.woff2') format('truetype');
    font-weight: 400;
}


:root {
    --color-white: #ffffff;
    --color-blue: #49C2E8;
    --color-black: #000000;
    --color-avatar-border: RGBA(255, 255, 255, 0.6);
    --color-placeholder: #B2B2B2;
}

::-webkit-scrollbar {
    width: 0;
    background: transparent;
}

* {box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: Geometria, sans-serif;
}

html,
body {

    font-synthesis: none;
    text-rendering: optimizeLegibility;
    font-family: Geometria, sans-serif;
    line-height: 1.5;
    background-color: var(--color-white);
    color: var(--color-black);
    margin: 0;
    padding: 0;
    max-width: 100vw;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

a {
    color: inherit;
    text-decoration: none;
}

ul,
li {
    margin: 0;
    padding: 0;
    list-style: none;
}

button{
    background-color: var(--color-white);
}